.page-header {
    position: relative;
    height: 100vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
}

.header-mask {
    backdrop-filter: brightness(50%) blur(5px);
    height: 100%;
    width: 100%;
    position: absolute;
}